<template>
	<div class="all">
		<Navbar :title='title'></Navbar>
		<div class="out">
			<div class="outer-swiper" >
			<MySwiper url='/static/shangPinlunbo.json' myHeight='230px'></MySwiper>
			</div>
		</div>
		<div class="product-desc">
			<ul v-for='(item,index) in gDetail' :key='item.id'>
				<li>
					<span class="product-desc-span">
					{{item.title}}
					</span>
				</li>
				<li class="price-li">
					市场价:<s>￥{{item.market_price}}</s>销售价:<span style="font-size:18px;">￥{{item.sell_price}}</span>
				</li>
				<li class="number-li">购买数量：<span class="s1" @click='substract'>-</span><span class="s2">{{pickNum}}</span><span class="s3" @click='add'>+</span></li>
				<li>
					<mt-button type="primary" size="small">立即购买</mt-button>
					<!-- 给“加入购物车加入单击事件” -->
					<mt-button type="danger" size="small" @click="ballHandler">加入购物车</mt-button>
				</li>
			</ul>
		</div>
		<transition name="ball" @after-enter='afterEnter'>
			<div class="ball" v-if="isExit"></div>
		</transition>
		<div class="product-props">
			<ul class="ul1" v-for='(item,index) in gDetail' :key='item.id'>
				<li>商品参数</li>
				<li>商品货号：{{item.goods_no}}</li>
				<li>库存情况：{{item.stock_quantity}}件</li>
				<li>上架时间：{{item.add_time|converTime('YYYY-MM-DD')}}</li>
			</ul>
			<div class="product-info">
				<ul>
					<li>
						<mt-button type="primary" size="large" plain @click.native='showShopInfo'>图文介绍</mt-button>
					</li>
					<li>
						<mt-button type="danger" size="large" plain @click.native='showComment'>商品介绍</mt-button>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import GoodsTool from '@/GoodsTool'
	export default{

		name:'GoodsDetail',

		data(){
			return{
				gDetail:[],
				pickNum:1,
				isExit:false,//小球默认是隐藏的
			};
		},
		props:['title'],
		methods:{
			showShopInfo(){
				//通过动态的路由进行路由的跳转
				this.$router.push({
					name:'photo.info',
					query:{
						id:this.$route.params.id
					}
				})
			},
			showComment(){
				this.$router.push({
					name:'good.comment',
					query:{
						id:this.$route.params.id
					}
				})
			},
			//数量--
			substract(){
				if(this.pickNum==0){
					return;
				}else{
					this.pickNum--;
				}
			},
			//数量++
			add(){
				if (this.pickNum<this.gDetail[0].stock_quantity) {
					this.pickNum++;
				}
			},
			ballHandler(){
				this.isExit=true;

			},
			afterEnter(){
				this.isExit=false;
				//触发公交车对象绑定的事件
				// this.$bus.$emit('sendPickNum',this.pickNum);

				this.$store.dispatch('addShopNumByAction',this.pickNum);
				//将添加到购物车的数据保存到本地存储对象中
				GoodsTool.add({
					// 
					id:this.gDetail[0].id,
					num:this.$store.getters.getShopNum,

				});
			}
		},
		created(){
			this.$axios.get('/static/goods/goodsDetail.json')
			.then(res=>{
				console.log(res.data);
				this.gDetail=res.data.message;
			}).catch(err=>{
				console.log("商品参数获取失败",err);
			})
		}
	};
</script>

<style lang="css" scoped="">
.all{
	margin-bottom: 100px;
}
.out{
	padding: 1px;
	border: 1px solid grey;
	margin: 3px;
	border-radius: 5px;
}
.outer-swiper{
	border-radius:10px; 
	border: 1px solid grey;
	margin: 2px;
}
.product-desc{
	padding: 5px;
	border: 1px solid grey;
	margin: 3px;
	border-radius: 5px;
}
.product-desc-span{
	width: 100%;
	display: block;
	font-weight: bolder;
	color: blue;
	border-bottom:1px solid grey;
	margin-top: 5px;
	margin-bottom: 5px;
}
.price-li{
	margin-top: 5px;
	margin-bottom: 5px;
}
.price-li s{
	margin-left: 20px;
	margin-right: 20px;
}
.price-li span{
	margin-left: 15px;
	color: red;
	font-weight: bold;
}
.number-li{
	margin-top: 5px;
	margin-bottom: 5px;
}
.ul1{
	padding: 5px;
	border: 1px solid grey;
	margin: 3px;
	border-radius: 5px;
	padding-bottom: 10px;
}
.ul1 li{
	margin-left: 10px;
	margin-top: 10px;
}
.product-info{
	padding: 5px;
	border: 1px solid grey;
	margin: 3px;
	border-radius: 5px;
}
.product-info li{
	margin-bottom: 3px;
}
.s1,.s3{
	border:1px solid grey;
	display: inline-block;
	width: 15px;
	text-align: center;
}
.s2{
	border:1px solid grey;
	display: inline-block;
	width: 30px;
	text-align: center;
	font-weight: bold;
}
.ball{
	width: 20px;
	height: 20px;
	background: red;
	border-radius: 10px;
	position: absolute;
	left: 25%;
	top: 270px;
}
.ball-enter-active{
	/*//给1秒的时间让小球进入动画效果*/
	animation: bounce-in 1s;
}
.ball-leave{
	/*元素进入后，透明度为0，整个动画都是0*/
	/*元素离开默认是1，所以会闪一下，设置为0*/
	opacity: 0;
}
@keyframes bounce-in{
	0%{
		transform: translate3d(0,0,0);
	}
	50%{
		transform: translate3d(130px,-180px,0);
	}
	75%{
		transform: translate3d(130px,0px,0);
	}
	100%{
		transform: translate3d(130px,400px,0);
	}
}
</style>


