<template>
	<div>
		<mt-swipe :auto="3000"  class='swiper' :style='{height:myHeight}'>
			<mt-swipe-item v-for = '(item,index) in imgs' :key='index'>
				<img :src="item.img||item.src" :class="{active:item.img}" alt="无信号">
			</mt-swipe-item>
		</mt-swipe>
	</div>
</template>

<script>
	export default{

		name:'MySwiper',

		data(){
			return{
				imgs:[]
			};
		},
		props:['url','myHeight'],
		created(){
			this.$axios.get(this.url)
			.then(res=>{
				this.imgs = res.data.message;
			})
			.catch(err=>{
				console.log("图片获取失败",err);
			})
		}
	};
</script>

<style lang="css" scoped="">
.active{
	width: 100%;
	height: 250px;
}

img{
	display: block;
	margin: 0 auto;
}
</style>


