<template>
	<div id="home">
		<!-- 轮播图4s -->
		<MySwiper url='/static/picture.json' myHeight='250px'></MySwiper>
		<!-- <mt-swipe :auto="3000" style='height:250px;' class='swiper'>
			<mt-swipe-item v-for = '(item,index) in imgs' :key='index'>
				<img :src="item.img" alt="无信号">
			</mt-swipe-item>
		</mt-swipe> -->
		<!-- 九宫格 -->
		<div class="list">
			<ul>
				<li v-for = '(grid,index) in grids' :key='grid.id'>
					<a href="javascript:void(0);">
						<router-link :to="grid.router">
							<img :src="grid.src" class="image">
						</router-link>
					</a>	
					<p class="p">{{grid.title}}</p>
				</li>
			</ul>
		</div>

	</div>
</template>


<script>
// assets中的文件在vue中的template/style下使用../这种相对路径的方式进行引入，
// script下必须用import的方式引入，而static下的文件在.vue的任何地方只要使用../这种相对路径的方式进行引入
	import src1 from '../../assets/news.png';
	import src2 from '../../assets/share.png';
	import src3 from '../../assets/show.png';
	import src4 from '../../assets/ask.png';
	import src5 from '../../assets/liuyan.png';
	import src6 from '../../assets/lianxi.png';
	var grids = [
		{id:1,src:src1,title:"新闻资讯",router:{name:'news.list'}},
		{id:2,src:src2,title:"图文分享",router:{name:'photo.list',params:{CategoryId:0}}},
		{id:3,src:src3,title:"商品展示",router:{name:'good.list',params:{page:1}}},
		{id:4,src:src4,title:"咨询",router:{name:'news.list'}},
		{id:5,src:src5,title:"留言反馈",router:{name:'news.list'}},
		{id:6,src:src6,title:"联系我们",router:{name:'news.list'}},
		// {id:7,src:src1,title:"新闻资讯"},
		// {id:8,src:src2,title:"图文分享"},
		// {id:9,src:src3,title:"商品展示"},
		// {id:10,src:src4,title:"咨询"},
		// {id:11,src:src5,title:"留言反馈"},
		// {id:12,src:src6,title:"联系我们"}
	]
	export default{

		name:'Home',
		data(){
			return{
				imgs:[],
				grids : grids,
			};
		},
		created(){
			// this.$axios.get('getlunbo')
			// .then(res=>{
			// 	console.log(res.data);
			// 	this.imgs = res.data.message;
			// })
			// .catch(err=>{
			// 	console.log('轮播图获取失败',err);
			// })
			// this.$axios.get('/static/picture.json')
			// .then(res=>{
			// 	this.imgs = res.data.message;
			// })
			// .catch(err=>{
			// 	console.log("图片获取失败",err);
			// })
		}
	};
</script>

<!-- 加了scoped只对当前闭合标签的组件起作用 -->
<style lang="css" scoped>
.swiper img{
	width: 100%;
	height: 250px;
}
.list{
	width: 100%;
}
.list ul{
	display: flex;
	flex-wrap: wrap;
	margin-bottom:50px;
	padding-bottom: 50px;
}
.list ul li{
	width: 33%;
	height: 60px;
	text-align: center;
	font-size: 13px;
	margin-top: 15px;
}
.list ul li a{
	display: block;
	width: 30px;
	height: 30px;
	margin: 0 auto;
}
.image{
	height: 30px;
	width:30px;
}
.p{
	font-weight: bold;
	margin-top: 10px;
}
</style>


