<template>
  <div id="app">
    <!-- 顶部栏 -->
    <mt-header fixed title="信息管理系统">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    <!-- 路由组件出口 一定要写-->
    <transition name='fade' mode='out-in'>
      <router-view/>
    </transition>


    <!-- 底部栏 -->
    <div class="tabBar">
      <ul>
        <li v-for="(tab,index) in tabs" :key='tab.id'>
          <router-link :to='tab.routername' @click.native='changeHash(index)' :class='{"link-active":index==currentIndex}'>
              <img :src="tab.imgSrc">
              <!-- //为购物车添加右上方的小球 -->
              <mt-badge size='small' color='#FC0107' v-if='index==2'>{{pickNum}}</mt-badge>
              <p>{{tab.title}}</p>
          </router-link>
        </li>
      </ul>
    </div>
    <!-- <mt-tabbar  v-model="selected" fixed>
        <mt-tab-item id="home">
          <img slot="icon" src="./assets/index.png">首页
        </mt-tab-item>
        <mt-tab-item id="vip">
          <img slot="icon" src="./assets/vip.png">会员
        </mt-tab-item>
        <mt-tab-item id="cart">
          <img slot="icon" src="./assets/car.png">购物车
        </mt-tab-item>
        <mt-tab-item id="search">
          <img slot="icon" src="./assets/search.png">查找
        </mt-tab-item>
    </mt-tabbar> -->

  </div>
</template>


<script>

import index from './assets/index.png'
import vip from './assets/vip.png'
import shopcart from './assets/car.png'
import search from './assets/search.png'
import GoodsTool from '@/GoodsTool'
let tabs = [
  {id:1,title:'首页',imgSrc:index,routername:{name:'home'}},
  {id:2,title:'会员',imgSrc:vip,routername:{name:'vip'}},
  {id:3,title:'购物车',imgSrc:shopcart,routername:{name:'cart'}},
  {id:4,title:'查找',imgSrc:search,routername:{name:'search'}}
];



export default {
  name: 'App',
  data(){
    return{
      selected:'',
      tabs:tabs,
      currentIndex:0,
      // pickNum:0 //底部栏购物车右上角的小球数字
    }
  },
  computed:{
    pickNum(){
      return this.$store.getters.getShopNum;
    }
  },
  methods:{
    changeHash(index){
      this.currentIndex=index;
    }
  },
  watch:{
    // 监听的是对应的ID值,selected是当前选中的ID
  selected:function(newV,oldV){
      // newV:点哪个就是哪个，oldV:上一次停留的那一个
      this.$router.push({name:this.selected});
    }
  },
  created(){
    //用公交车对象绑定事件
    // this.$bus.$on('sendPickNum',(data)=>{
    //   this.pickNum+=data;
    // });
    // this.pickNum=GoodsTool.getTotalCount()
    this.$store.dispatch('changeShopNumByAction',GoodsTool.getTotalCount());
  }
}
</script>
<style scoped>
.fade-enter-active,.fade-leave-active{
  transition:opactity 1s;
}
.fade-enter,.fade-leave-to{
  opactity:0;
}
.tabBar{
  width:100%;
  height:55px;
  background-color:#CCCCCC;
  position:absolute;
  bottom:0;
  left:0;
  background-image:linear-gradient(180deg,#d9d9d9,#d9d9d9 50%,transparent 50%);
  background-size:100% 1px;
  background-repeat:no-repeat;
  background-position:top left;
  background-color:#fafafa;
}
.tabBar ul{
  width:100%;
  overflow:hidden;
}
.tabBar ul li{
  float:left;
  width:25%;
  height:55px;
  text-align:center;
}
.tabBar ul li a{
  display:inline-block;
  width:100%;
  height:100%;
  padding-top:10px;
  position:relative;
}
.tabBar ul li a.link-active{
  background-color:green;
  color:white;
}
.tabBar ul li p{
  font-size:12px;
}
.tabBar ul li img{
  width:25px;
  height:25px;
}
.mint-badge.is-size-small{
  position:absolute;
  top:0;
  right:10px;
}
</style>




