<template>
	<div>
		<Navbar title='购物车'></Navbar>
		<div class="pay-detail">
			<ul>
				<li class="p-list" v-for="(shop,index) in shopCar" :key='shop.id'>
					<mt-switch v-model='shop.idSelect'></mt-switch>
					<img :src="shop.thumb_path">
					<div class="pay-calc">
						<p>{{shop.title}}</p>
						<div class="calc">
							<span class="s1">￥{{shop.sell_price}}</span>
							<span class="s2" @click='substract(shop)'>-</span><span class="s3">{{shop.num}}</span><span class="s4" @click='addnum(shop)'>+</span>
							<a href="javascript:void(0);" @click='del(shop,index)'>删除</a>
						</div>
					</div>	
				</li>
			</ul>
		</div>
		<div class="show-price">
			<div class="show-1">
				<p>总计(不含运费)：</p>
				<span>已选商品{{payment.count}}件，总价￥{{payment.total}}元</span>
			</div>	
			<div class="show-2">
				<mt-button type='danger' size='large'>去结算</mt-button>
			</div>
		</div>
	</div>
</template>

<script>
import GoodsTool from '@/GoodsTool'
	export default{
		name:'Cart',

		data(){
			return{
				shopCar:[],
			};
		},
		methods:{
			substract(shop){
				if (shop.num==0) {
					return;
				}
				shop.num--;
				this.$store.dispatch('addShopNumByAction',-1);
			},
			addnum(shop){
				shop.num++;
				this.$store.dispatch('addShopNumByAction',1);
			},
			del(shop,index){
				//删除数组元素
				this.shopCar.splice(index,1);
				//删除shop
				// delete GoodsTool[shop.id];
				GoodsTool.removeGoods(shop.id);
				localStorage.clear();
				let num = shop.num;
				this.$store.dispatch('addShopNumByAction',-num);
			}
		},
		beforeRouteLeave(to,from,next){
			//导航离开该组件的对应路由时调用
			//可以访问组件实例'this'
			let res=confirm('你确定要离开吗？');
			if (res) {
				//保存用户的编辑数据
				let obj={};
				this.shopCar.forEach(shop=>{
					obj[shop.id]=shop.num;
				})
				GoodsTool.saveGoods(obj);
				next();
			}else{
				next(false);//取消导航
			}
		},
		computed:{
			payment(){
				//默认只有getter方法
				let total=0;//总钱数
				let count=0;//总个数
				this.shopCar.forEach(shop=>{
					if(shop.idSelect){
						//如果勾选就去计算
						count+=shop.num;
						total+=shop.num*shop.sell_price
					}
				});
				return{
					total,count,
				};
			}
		},
		created(){
			console.log(GoodsTool.getGoodsList());
			let goodsList = GoodsTool.getGoodsList();
			//获取出来是这样的{{"101":5}}
			let ids=Object.keys(goodsList).join(',');
			if (ids) {
				//发起请求 获取用户购买商品的列表
				this.$axios.get("/static/goods/shopcart"+ids+".json")
				.then(res=>{
					this.shopCar=res.data.message;
					//vue会将shopCart中的对象的所有属性进行监视，完成响应式
					// Object.defineProperty(this,'shopCart',{
					// 	set:function(){
					// 		判断shopCart元素是否有属性
					// 	}
					// })shopCart是被监听的


					//给数组的元素添加属性
					for(var i=0;i<this.shopCar.length;i++){
						let shop = this.shopCar[i];
						let num = goodsList[shop.id];
						if (num) {
							// 如果数据不完整的情况下要添加属性，就需要手动通知vue来完成响应式==>意味着要双向数据绑定
							//下面是通知
							//前端要向后台添加数据的话要使用这个通知，双向数据绑定
							this.$set(shop,'num',num);
							this.$set(shop,'isSelect',true);
							shop.num = num;
							shop.isSelect=true;
						}
					}
				}).catch(err=>{
					console.log("购物车信息获取失败",err);
				})
			}
		}
	};
</script>

<style lang="css" scoped="">
.p-list{
	width: 100%;
}
.p-list img{
	width: 100px;
	height: 100px;
}
.pay-calc	p{
	font-size: 18px;
	font-weight: bold;
	margin-top: 10px;
	margin-bottom: 10px;
	color: blue;
}	
.s1{
	font-weight: bold;
	margin-right: 15px;
}	
.s2{
	display: inline-block;
	border:1px solid grey;
	text-align: center;
	width: 15px;
}
.s3{
	border:1px solid grey;
	display: inline-block;
	text-align: center;
	width: 25px;
}
.s4{
	border:1px solid grey;
	display: inline-block;
	width: 15px;
	text-align: center;
}
.calc a{
	margin-left: 15px;
}
.show-price{
	margin-top: 20px;
	background: grey;

}
.show-1{
	font-size: 17px;
	margin-left: 10px;
}
</style>


