<template>
	<div class="header">
		<h3 @click='goBack' class="back">◀</h3>
		<h3 class="desc_title">
			{{title}}
		</h3>
	</div>
</template>
<script>
	export default{
		name:'Navbar',
		props:['title'],
		data(){
			return{

			};
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			}
		}
	};
</script>
<style type="css" scoped>
	.header{
		width: 100%;
		height: 30px;
		line-height: 30px;
		position: relative;
		background-color: white;
	}
	h3{
		float: left;
	}
	.back{
		margin-left: 10px;
		color: black;
	}
	.desc_title{
		position: absolute;
		width: 200px;
		height: 40px;
		left: 50%;
		margin-left: -100px;
		text-align: center;
	}
</style>


