import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
	state:{
		//小球的数量默认为1
		num:0,
	},
	getters:{
		getShopNum(state){
			return state.num;
		}
	},
	mutations:{
		addShopNum(state,num){
			state.num +=num
		},
		changeShopNum(state,num){
			state.num = num;
		}
	},
	actions:{
		addShopNumByAction({commit},num){
			commit('addShopNum',num)
		},
		changeShopNumByAction({commit},num){
			commit('changeShopNum',num)
		}
	}
});


