<template>
	<div>
		<div class="photo-title">
			<Navbar title='图文详情'></Navbar>
			<p>{{imgInfo.title}}</p>
			<span>发起时间：{{imgInfo.add_time|converTime('YYYY-MM-DD')}}</span>
			<span>{{imgInfo.click}} 次浏览</span>
			<span>分类:民生经济</span>
			
			<!-- <ul>
				<li v-for='(item,index) in thumImgs' :key='index' >
					<a href="javascript:void(0);">
						<img :src="item.src">
					</a>
				</li>
			</ul> -->
			<vue-preview :slides='thumImgs'></vue-preview>
			
			

			<!-- 详细内容 -->
			<div class="photo-desc">
				<p v-html='imgInfo.content'></p>
			</div>

			<!-- 使用全局的评论组件 -->
			<!-- Comment:cid='$router.query.id' -->
			<Comment :cid='this.$route.query.id'></Comment>
		</div>
	</div>
</template>

<script>
	export default{

		name:'PhotoDetail',

		data(){
			return{
				imgInfo:{},
				thumImgs:[]
			};
		},
		created(){
			let id =this.$route.query.id;
			//获取图文详情
			// console.log(id);
			//如果使用普通函数声明当前this为undefined建议使用箭头函数改变当前this的指向
			var getImageInfo=()=> {
			  	return this.$axios.get('/static/message/ImageInfo'+id+'.json');
			}
			//获取缩略图
			var getThumImages=()=> {
			  	return this.$axios.get('/static/message/ThumImages'+id+'.json');
			}

			this.$axios.all([getImageInfo(), getThumImages()])
			.then(this.$axios.spread( (acct, perms)=> {
			    // 两个请求现在都执行完成
			    this.imgInfo = acct.data.message[0];
			    this.thumImgs = perms.data.message;
			    this.thumImgs.forEach((item,index)=>{
			    	item.msrc = item.src;
			    	item.w=600;
			    	item.h=600;
			    })

			}));
		}
	};
</script>

<style lang="css" scoped="">
.photo-title p{
	font-size: 20px;
	font-weight: 600;
	color: #0F80FF;
	margin: 15px 0;
	margin-left: 10px;
}
.photo-title span{
	padding: 10px 5px;
	font-size: 13px;

}
.photo-desc p{
	font-size: 16px;
	color: #333333;
	clear: both;
}
.photo-title ul{
	width: 100%;
}
.photo-title ul li{
	width: 30%;
	margin: 5px;
}
.photo-title ul li{
	float: left;
}
.photo-title ul li a{
	display: inline-block;
}
.photo-title ul li a img{
	width: 100%;
}

/*.photo-title{
	width: 100%;
	overflow: hidden;
	margin-top: 20px;
}*/
</style>


