<template>
	<div class="tmpl">
		<div class="photo-bottom">
			<ul>
				<li class="photo-comment">
					<div>
						<span>提交评论</span>
						<span><a >返回</a></span>
					</div>
				</li>
				<li class="txt-comment">
					<textarea cols="50" rows="10" v-model='commentContent'></textarea>
				</li>
				<li>
					<mt-button type="primary" size='large' @click='commentHandler'>评论</mt-button>
				</li>
				<li class="photo-comment">
					<div>
						<span>评论列表</span>
						<span>44条评论</span>
					</div>
				</li>
			</ul>
			<ul class="comment-list">
				<li v-for='(item,index) in comment' :key='index'>
					<p>&nbsp;{{item.user_name}}：{{item.content}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;   {{item.add_time|relativeTime}}</p>
				</li>
			</ul>
			<mt-button type="danger" size='large' @click='loadMore'>加载更多</mt-button>
		</div>
	</div>
</template>

<script>
	export default{
		name:'Comment',
		data(){
			return{
				comment:[],
				page:1,
				commentContent:''
			};
		},
		methods:{
			//评论按钮的事件操作
			commentHandler(){
				// //post请求
				// this.$axios.post('api','content='+this.commentContent)
				// .then(res=>{
				// 	console.log(res.data)
				// 	//发表之后，清空评论框
				// 	this.commentContent='';
				// 	//加载第一页数据
				// 	this.page=1;
				// 	this.loadMore();
				// }).catch(err=>{
				// 	console.log("评论失败",err);
				// })
			},
			//加载更多按钮的时间操作
			loadMore(page){
				console.log(this.page);
				this.$axios.get('/static/message/Comment'+this.cid+'？pageindex='+this.page+'.json')
				.then(res=>{
					if (res.data.message==null) {
						this.$toast("没有数据咯");
					}
					console.log(res.data);
					if (page) {
						//表示加载更多
						this.comment = this.comment.concat(res.data.message);
					}
					else{
						//表示第一次加载
						this.comment=res.data.message;
					}
					this.page++;
				}).catch(err=>{
					console.log("评论分页获取失败",err);
				})
			}
		},
		props:['cid'],
		created(){
			
			//第一次加载
			this.loadMore();
			// let page = this.$route.query.page||'1';
			
		}
	};
</script>

<style lang="css" scoped="">
.tmpl{
	padding-bottom: 100px;
	width: 100%;
}

.txt-comment textarea{
	width: 99%;
}
.photo-comment > div span:nth-child(1){
	float: left;
	font-weight: bold;
	margin-left: 5px;
}
.photo-comment > div span:nth-child(2){
	float: right;
}
.comment-list{
	clear: both;
	border-left: 1px solid grey;
	border-right: 1px solid grey;
	border-bottom: 1px solid grey;
}
.comment-list li{
	border-top: 1px solid grey;
	font-size: 13px;
}
.comment-list li p{
	padding: 2px;
	font-weight: 600;
}

</style>


