import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
// 使用vue-router插件  Vue.prototype.$router=Router;
// @表示src

//引入
import Home from '@/components/Home/Home'
import Vip from '@/components/Vip/Vip'
import Cart from '@/components/Cart/Cart'
import Search from '@/components/Search/Search'
import NewsList from '@/components/News/NewsList'
import NewsDetail from '@/components/NewsDetail/NewsDetail'
import PhotoList from '@/components/Photo/PhotoList'
import PhotoDetail from '@/components/Photo/PhotoDetail'
import GoodsList from '@/components/Goods/GoodsList'
import GoodsDetail from '@/components/Goods/GoodsDetail'
import TuWenJieShao from '@/components/Goods/TuWenJieShao'
import GoodsComment from '@/components/Goods/GoodsComment'


Vue.use(Router)

export default new Router({
	//匹配路由规则
  linkActiveClass:'link-active',//给所有a标签link-active,实现给选中的底部栏加上颜色
  routes: [
    {
      path:'/',
      redirect:'/home'
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/vip',
      name: 'vip',
      component: Vip
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/search',
      name: 'search',
      component: Search
    },
    //新闻列表
    {
    	path:'/news/list',
    	name:'news.list',
    	component:NewsList
    },
    //新闻详情
    {
      path:'/news/detail',
      name:'detail',
      component:NewsDetail
    },
    {
      //动态路由
      path:'/photos/list/:CategoryId',
      name:'photo.list',
      component:PhotoList
    },
    {
      path:'/photos/detail',
      name:'photo.detail',
      component:PhotoDetail
    },
    {
      //商品列表
      path:'/goods/list/:page',
      name:'good.list',
      component:GoodsList
    },
    {
      //商品详情
      path:'/goods/list',
      name:'good.detail',
      component:GoodsDetail,
      props:{
        title:'商品详情'
      }
    },
    {
      //商品图文介绍
      path:'/goods/list/photo/info',
      name:'photo.info',
      component:TuWenJieShao,
      // 路由组件传参
      props:{
        title:'商品图文详情'
      }
    },
    {
      path:'/goods/comment',
      name:'good.comment',
      component:GoodsComment
    }
  ]
})



