// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'
import Moment from 'moment'
import VuePreview from 'vue-preview'
import store from './store'
 
// defalut install
//全局使用图片查看器
Vue.use(VuePreview);//内部会运行Vue.component('vue-preview',{实例化对象})

Vue.config.productionTip = false

// 使用mint-ui 引入全部的组件
import Mint from 'mint-ui';
Vue.use(Mint);
import 'mint-ui/lib/style.css';
/* eslint-disable no-new */

//配置公共的URL
// Axios.defaults.baseURL = 'http://www.sinya.online/api/';
//配置axios,将axios挂载到vue父级实例对象上面
Vue.prototype.$axios = Axios;

//注册全局的导航栏组件
import Navbar from '@/components/Common/Navbar.vue'
Vue.component(Navbar.name,Navbar)//注册

//注册全局的轮播图组件
import MySwiper from '@/components/Common/MySwiper.vue'
Vue.component(MySwiper.name,MySwiper)//注册

import Comment from '@/components/Common/Comment.vue'
Vue.component(Comment.name,Comment)//注册

//自定义moment全局的过滤器
Vue.filter('converTime',function(data,formatStr){
	return Moment(data).format(formatStr);
});
Vue.filter('relativeTime',function(data){
	return Moment(data).fromNow();
})
//设置中文显示
Moment.locale('zh-cn');
//控制字数显示的
Vue.filter('controllShow',function(str,num){
	//如果当前字符串的长度小于num，返回原值
	if (str.length<=num) {
		return str;
	}
	//如果当前字符串的长度大于num，则截取0-num-1位
	if(str.length>num){
		return str.substr(0,num-1)+'…';
	}
});

// 添加请求拦截器
Axios.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    //加载提示框
    Mint.Indicator.open({
    	text:'玩命加载中…'
    });
    return config;
  }, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  });

// 添加响应拦截器
Axios.interceptors.response.use(function (response) {
	//关闭加载提示框
	Mint.Indicator.close();
    // 对响应数据做点什么
    return response;
  }, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  });

// 将bus对象挂载到Vue原型上公交车对象
// $bus是个名字
import EventBus from './EventBus.js'
Vue.prototype.$bus = EventBus;



//引用自己书写的全局css样式
import '../static/css/global.css'

new Vue({
  el: '#app',
  router,
  store,//
  components: { App },
  template: '<App/>'
})



