<template>
	
		<div class="page-loadmore">
			<Navbar title='商品列表'></Navbar>
			<div class="page-loadmore-wrapper" ref='wrapper' :style='{height:wrapperHeight+"px"}'>
				<mt-loadmore :bottom-method='loadBottom' @bottom-status-change='handleBottomChange' :bottom-all-loaded='allLoaded' ref='loadmore' :autoFill='false' :auto-fill='autoFill'>
				<ul class="page-loadmore-list">
					<li v-for='(item,index) in gList' :key='item.id'>
						<router-link :to='{name:"good.detail",params:{id:"goods_id"}}'>
							<img :src="item.img_url">
							<div class="title">{{item.title|controllShow(10)}}</div>
							<div class="desc">
								<div class="sell">
									<span>￥{{item.sell_price}}</span>
									<s>￥{{item.market_price}}</s>
								</div>
								<div class="detail">
									<div class="hot">
										热卖中
									</div>
									<div class="count">
										剩余{{item.stock_quantity}}件
									</div>
								</div>
							</div>
						</router-link>
					</li>
				</ul>	
			</mt-loadmore>
			</div>
		</div>
</template>

<script>
	export default{

		name:'GoodsList',

		data(){
			return{
				gList:[],
				allLoaded:false,
				page:1,
				wrapperHeight:0,
				autoFill:false
			};
		},
		methods:{
			//上拉加载的事件
			loadBottom(){
				console.log("上拉调用了");
				this.loadGoodsByPage();
				//通知状态发生改变
				this.$refs.loadmore.onBottomLoaded();
			},
			handleBottomChange(status){
				console.log(status);
			},
			loadGoodsByPage(){
				this.$axios.get('/static/goods/getgoods/pageindex='+this.page+'.json')
				.then(res=>{
					if(res.data.message.length<6){
						//所有数据都加载完成
						this.$toast("没有数据了");
						//禁止上拉，不需要再更新数据
						this.allLoaded=true;
					}
					if(this.page==1){
						this.gList=res.data.message;
					}else{
						this.gList= this.gList.concat(res.data.message);
					}
					this.page++;
					
				}).catch(err=>{
					console.log("获取商品列表失败",err);
				})
			}
		},
		created(){
			//加载第一页商品列表的数据
			this.loadGoodsByPage();
		},
		mounted(){
			this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
		}

	};
</script>

<!-- mt-loadmore中的属性
1.bottomLoad函数
2.机制bottomLoad :bottom-all-loaded 默认为false，可以上拉，true禁止上拉
3.auto-fill默认为true，自动检测父容器，并调用bottomload直到撑满父容器
4.pull拉动未满足 70px，drop达到70px。loading加载中
5.loadmore组件对象的onBottomLoaded()通知结束loading进入pull状态
6.在组件上写ref在js中通过this.$reds.XXX获取的组件对象
通过在普通标签写ref在js中通过this$refs.XXX获取的dom对象
7.上拉加载更多的公式
进入检测机制==>可视的高度 + 页面卷起的高度 = 设备的高度 -->


<style lang="css" scoped="">
*{
	padding: 0;
	margin: 0;
}
.page-loadmore{
	width: 100%;
	margin-bottom: 100px;
}
.page-loadmore-wrapper{
	overflow: scroll;
	touch-action:auto;
}
.page-loadmore-list{
	width: 100%;
}
.page-loadmore-list li{
	width: 41%;
	float: left;
	margin-top: 5%;
	margin-left: 6%;
	border: 1px solid grey;
	padding-bottom: 5px;
}
.page-loadmore-list li img{
	width: 100%;
}
.title{
	font-size: 13px;
	font-weight: 600;
	margin-left: 2px;
	color: #000;
}
.sell{
	font-size: 13px;
}
.desc{
	width: 100%;
}
.sell span{
	font-size: 14px;
	color: red;
}
.sell s{
	font-size: 12px;
	margin-left: 8px;
	font-weight: 600;
	color: grey;
}
.detail{
	margin-top: 10px;
}

.hot{
	color: orange;
	float: left;
	font-size: 14px;
}
.count{
	color: orange;
	float: right;
	font-size: 14px;
}
</style>


