<template>
	<div class="news">
		<!-- {{newsList}} -->
		<Navbar title='新闻列表'></Navbar>
		<div class="newList">
			<ul>
				<li v-for="(news,index) in newsList" :key='news.id'>
					<router-link :to='{name:"detail",query:{id:news.id}}'>
						<div class="new_img">
							<img :src="news.img_url" alt="">
						</div>
						<div class="content">
							<p class="title">{{news.title}}</p>
							<div class="news-desc">
								<p class="summary">作者：{{news.author}}</p>
								<p class="lasttwo">
									<span class="praise">点赞数：{{news.click}}</span>&nbsp;&nbsp;&nbsp;
									<span class="time">发表时间：{{news.add_time|converTime('YYYY-MM-DD')}}</span>
								</p>
							</div>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	export default{

		name:'NewList',

		data(){
			return{
				newsList:[]
			};
		},
		created(){
			this.$axios.get('/static/news.json')
			.then(res=>{
				this.newsList = res.data.newData;
			})
			.catch(err=>{
				console.log("图片获取失败",err);
			})
		}
	};
</script>

<style lang="css" scoped="">
	.news{
		width: 100%;
		margin-bottom: 110px;
	}
	..newList{
		width: 100%;
	}
	.newList ul{
		width: 100%;
	}
	.newList ul li{
		width: 100%;
		border-bottom: 1px solid #CCCCCC;
		height: 100px;
		overflow: hidden;
	}
	.newList ul li a img{
		width: 35%;
		float: left;
		margin-top: 10px;
		margin-left: 5px;
	}
	.content{
		float: left;
		width: 60%;
		margin-top: 10px;
		margin-left: 5px;
	}
	.title{
		font-weight: bold;
		font-size: 14px;
		color: black;
	}
	.summary{
		margin-top: 10px;
		font-size: 14px;
		color: red;
		font-weight: bold;
	}
	.lasttwo{
		margin-top: 5px;
		font-size: 12px;
		color: orange;
	}
</style>


